import Vue from 'vue'
import Router from 'vue-router'
import List from '@/components/forum/List'
import Detail from '@/components/forum/Detail'

Vue.use(Router)

export default new Router({
  linkActiveClass: 'active',
  hashbang: false, // #!
  history: false, // HTML5 historypushStatereplaceState
  mode: 'history',
  /**
   * @desc 
   */
  routes: [
    {
      path: '/forum/detail',
      name: 'Detail',
      component: Detail
    },
    {
      path: '/forum/list',
      name: 'List',
      component: List
    },
    {
      path: '/forum',
      redirect: '/forum/list'
    }
  ]
})



